.First{
  width: 100%;
  position: static;
}
.maincard{
    margin-top: 2%;
    margin-bottom: 3%;
    width: 19%;
    height: 380px;
    position:static;
    margin-left: 5%;
    float: left;
}
.maincard .card1{
   
    position: relative;
    width: 100%;
    height: 95%;
    box-shadow: 1px 1px 5px 1px brown;
    border-radius:4%;
    background-color: rgb(224, 224, 203);

    /* background: #232323; */
    overflow: hidden;
    float:left;
    margin-right:5%  
}
.maincard .card1 .Img{
    padding:20px 0px 10px 0px;
    height: 86%;
    width: 105%;
    transition: ease;
    position: absolute;
    transition-duration: 0.5s;
    transition-delay: 0.s;
}

 .maincard .card1:hover .Img {
    padding:10px 0px 20px 0px;
    display: absolute;
    transition: 0.8;
    transition-delay: 0.1s;
    height: 75%;
    width: 100%;
    
}

.maincard .card1::before{
   
    content: '';
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background-color:rgb(218, 23, 23);

    /* background-color: slateblue; */
    /* background: #9bdc25; */
    clip-path: circle(76% at 80% 20%);
    transition: 0.5s ease-in-out;
}
.maincard .card1:hover::before{
    clip-path: circle(88% at 80% -5%);
    
}
.maincard .card1 .TextContent{
   
     position:absolute;
     bottom: 0;
     width: 100%;
     height: 100px;
     text-align: center;
     transition: 1s;
     z-index: index 10;
     transition-delay:0.2s ;

}
.maincard .card1:hover .TextContent{
    height: 160px;
    
}
.maincard .card1 .TextContent .Name{

    position: relative;
    font-weight: bold;
    letter-spacing: 1px;
    color: darkblue;
    font-size: 20px;
    padding-top: 20%;
}
/* .maincard .card1 .TextContent:hover .Name{
    padding-top: 35px;
} */

.maincard .card1 .TextContent .btn{
    display: inline-block;
    padding: 10px 20px;
    background-color:rgb(218, 23, 23);
    border-radius: 4px;
    margin-top:10px;
    margin-right: 12px;
    text-decoration: none;
    font-weight: 200;
    color:#fff;
    border-radius: 8%;
    opacity: 0;
    transform:  translateY(50px);
    transition: 0.5s;
}
.maincard .card1:hover .TextContent .btn{
    opacity: 1;
    transform: translateY(0px);
    cursor: pointer;
}
